import {
  Component,
  OnInit,
  Injector,
  ViewChild,
  ViewChildren,
  QueryList,
  ElementRef,
} from '@angular/core';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { BaseForm } from 'src/app/class/base.form';
import { environment } from 'src/environments/environment';
import { URLz } from 'src/app/enums/url.enum';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './ready-for-erp.component.html',
  styleUrls: ['./ready-for-erp.component.css'],
})
export class ReadyForErpComponent extends BaseForm implements OnInit {
  @ViewChildren('checkboxes') checkboxes: QueryList<ElementRef>;
  @ViewChild('parencheckbox') parencheckbox: ElementRef;
  public dataSource = new MatTableDataSource();
  @ViewChild(MatPaginator) paginator: MatPaginator;

  public cashCheque = [
    'S No',
    'Select',
    'Receipt_No',
    'Receipt_Date',
    'Amount',
    'Donation_Sub_type',
    'Cheque_Date',
    'Cheque_no',
    'Bank',
    'Despoit_Slip_No',
  ];
  public material = [
    'S No',
    'Select',
    'Receipt_No',
    'Receipt_Date',
    'Amount',
    'Donation_Sub_type',
    'store_location',
    'Item',
    'Weight',
    'qty',
    'Unit',
  ];
  public filter_data = {};
  public idList = [];
  public submitedArrayList = [];

  // table paginator data

  totalRows = 0;
  pageSize = 10;
  currentPage = 0;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  // table paginator data

  public displayedColumns: Array<any | string> = [];
  public selectAllList: Array<any | number> = [];
  public multiSelect: boolean = false;
  public showTable = false;
  totalRecords: number;

  constructor(injector: Injector) {
    super(injector);
  }

  ngOnInit(): void {
    this.dataSource.paginator = this.paginator;
    this._component = 'Mark Ready For ERP';
    this.initForm();
  }

  initForm() {
    this._fs._form = this._fb.group({
      receipt_type: [''],
      details: this._fb.array([]),
    });
  }

  getStoreFilter(event: any) {
    this.filter_data = event;
    this.disPlay_column(event);
    if (event !== '' && event !== undefined && event !== null) {
      this._http
        .get({
          url: environment.TRANSACTION,
          endpoint: URLz.READY_FOR_ERP,
          query: {
            from_date: event.selected.startDate,
            to_date: event.selected.endDate,
            bg: event.bg,
            le: event.le,
            ou: event.ou,
            su: event.su,
            receipt_type: event.receipt_type,
            donation_type: event.donation_type,
            limit: this.pageSize,
            page: this.currentPage + 1,
          },
        })
        .subscribe((res: any) => {
          this.paginator.pageIndex = this.currentPage;
          this.totalRows = res?.data?.totalRecords;
          this._fs._form
            .get('receipt_type')
            .patchValue(
              res?.data?.records.length > 0
                ? res?.data?.records[0].receipt_type_no
                : ''
            );
          this.dataSource.data = res?.data?.records;
          this.selectAllList = res?.data?.records;
          this.totalRecords = res?.data.totalRecords;
          res?.data?.records.length > 0 ? (this.showTable = true) : (
            this.showTable = false,
            this._swl.noDataFound()
          );
        });
    }
  }

  disPlay_column(event) {
    if (event.receipt_type == 1 || event.receipt_type == 3) {
      this.displayedColumns = this.cashCheque;
    } else if (event.receipt_type == 4) {
      this.displayedColumns = this.material;
    }
  }

  selectAll() {
    var selectedPerArr: FormArray = this._fs._form.get('details') as FormArray;
    (this._fs._form.get('details') as FormArray).clear();
    this.multiSelect = !this.multiSelect;
    if (this.multiSelect) {
      if (this.selectAllList.length != 0) {
        this.selectAllList.forEach((val) => {
          selectedPerArr.push(
            new FormGroup({
              id: new FormControl(val.id),
              master_ids: new FormControl(val.master_id),
            })
          );
        });
      }
    } else {
      while (0 !== selectedPerArr.length) {
        selectedPerArr.removeAt(0);
      }
    }
  }

  getcheckBoxValue(val, id, master_id) {
    const selectedIdArr: FormArray = this._fs._form.get('details') as FormArray;
    if (val.target.checked) {
      selectedIdArr.push(
        new FormGroup({
          id: new FormControl(id),
          master_ids: new FormControl(master_id),
        })
      );
    } else {
      let i: number = 0;
      selectedIdArr.controls.forEach((ctrl: FormGroup) => {
        if (ctrl.value.id == id && ctrl.value.master_ids == master_id) {
          selectedIdArr.removeAt(i);
          return;
        }
        i++;
      });
    }
    this.indeterminate(selectedIdArr.value);
  }

  indeterminate(checked_arr) {
    if (checked_arr.length == this.totalRecords) {
      this.parencheckbox.nativeElement.checked = true;
    } else if (checked_arr.length == 0) {
      this.parencheckbox.nativeElement.checked = false;
    }
  }

  uncheckAll() {
    this.parencheckbox.nativeElement.checked = false;
    this.checkboxes.forEach((element) => {
      element.nativeElement.checked = false;
    });
  }

  pageChanged(event: PageEvent) {
    this.pageSize = event.pageSize;
    this.currentPage = event.pageIndex;
    this.getStoreFilter(this.filter_data);
    (this._fs._form.get('details') as FormArray).clear();
    this.multiSelect = false;
  }

  checksForSubmit() {
    this.multiSelect = false;
    this.uncheckAll();
    (this._fs._form.get('details') as FormArray).clear();
  }

  _onSubmit() {
    let data;
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if (this._fs._form.valid) {
      this._http
        .create({
          body: this._fs._form.value,
          url: environment.DEPOSITE_CASH,
          endpoint: URLz.READY_FOR_ERP,
        })
        .subscribe({
          next: (res: any) => {
            data = res;
            if (res.data != undefined && res.data != null) {
              if (res?.data?.errors?.length > 0) {
                for (let i = 0; i < res?.data?.errors?.length; i++) {
                  this.idList[i] = res?.data.errors[i]?.fund_category[0]?.id;
                }
                this.checksForSubmit();
                if (res?.data?.success?.length > 0) {
                  Swal.fire({
                    title: this._translate.instant('Processed'),
                    text: this._translate.instant(res.message),
                    icon: 'success',
                    confirmButtonColor: '#3085d6',
                    confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
                  });
                }
                this.getStoreFilter(this.filter_data);
                this._vs._error_from_success(data);
              } else {
                this.checksForSubmit();
                if (res?.data?.success?.length > 0) {
                  Swal.fire({
                    title: this._translate.instant('Processed'),
                    text: this._translate.instant(res.message),
                    icon: 'success',
                    confirmButtonColor: '#3085d6',
                    confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
                  });
                }
                this.getStoreFilter(this.filter_data);
              }
            }
          },
          error: (httpErrorResponse: HttpErrorResponse) => {
            this._vs._error_server(httpErrorResponse.error);
          },
        });
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }
}
