import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ACTION } from 'src/app/enums/action.enum';
import { ReadyForErpComponent } from './ready-for-erp.component';


const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'ready_for_erp',
        pathMatch: 'full'
      },
      {
        path: "ready_for_erp",
        component: ReadyForErpComponent,
        data: {
          permission: [
            ACTION.EDIT,
            ACTION.ADD
          ],
          title: 'Mark Ready for ERP'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MarkReadyForErpRoutingModule { }
